//https://developers.google.com/custom-search/docs/element#html5
//https://stackoverflow.com/questions/46136765/using-google-custom-search-engine-in-reactjs

import React from 'react';

export default class GoogleSearch extends React.Component {
    constructor(props){
        super(props)
    }

    componentDidMount() {
        var myCallback = function () {
            if (document.readyState == 'complete') {
                //render search box
                google.search.cse.element.render(
                    {
                        div: "root",
                        tag: 'search'
                    });
            } else {
                //render result
                google.setOnLoadCallback(function () {
                    google.search.cse.element.render(
                        {
                            div: "root",
                            tag: 'search'
                        });
                }, true);
            }
        };

        window.__gcse = {
            parsetags: 'explicit',
            callback: myCallback
        };

        //load and config google search engine
        (function (cx) {
            //var cx = '008391824253360889328:j5posmpyok0';// Insert your own Custom Search engine ID here
            
            var gcse = document.createElement('script');
            gcse.type = 'text/javascript';
            gcse.async = true;
            gcse.src = 'https://cse.google.com/cse.js?cx=' + cx;
            var s = document.getElementsByTagName('script')[0];
            s.parentNode.insertBefore(gcse, s);
        })(this.props.searchEngineID);
    }

    render() {
        return (
            <div className="content">
                <div className="gcse-searchbox" data-resultsUrl="http://www.vnua.edu.vn" 
                    data-newWindow="true" 
                    data-queryParameterName="search" >
                </div>
            </div>
        );
    }
}



